const createMeeting = (
  i: number,
  x: number,
  y: number,
  w: number,
  h: number
) => ({
  [`meeting${i}`]: {
    type: "meeting",
    position: [x, y],
    size: [w, h],
    zIndex: 0,
    background: "",
    border: "",
    iframe: "",
  },
});

const createGoBoard = (i: number, x: number, y: number) => ({
  [`gomeeting${i}`]: {
    type: "meeting",
    position: [x, y],
    size: [250, 150],
    zIndex: 0,
    background: "",
    border: "",
    iframe: "",
  },
  [`gobard${i}`]: {
    type: "goboard",
    position: [x, y],
    size: [250, 150],
    zIndex: 0,
    background: "rgba(140, 140, 140, 0.2)",
    border: "",
    iframe: "",
  },
});

